<template>
    <div>
        <head-top webActive="home"></head-top>
        <div class="bgGray" id="orderConfirm">
            <div class="container">
                <el-breadcrumb separator-class="el-icon-arrow-right" class="mb10">
                    <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
                    <el-breadcrumb-item>确认订单</el-breadcrumb-item>
                </el-breadcrumb>
                <div class="orderBg">
                    <h1 class="order_tit">送货方式</h1>
                    <div class="o_sendWay">
                        <el-radio-group v-model="form.distribution">
                            <el-radio label="normal" border>普通物流</el-radio>
                            <el-radio label="self" border>自提</el-radio>
                        </el-radio-group>
                    </div>
                    <h1 class=" flex f-nowrap j-bet mb10 mt30" v-if='form.distribution == "normal"'>
                        <span class="order_tit mt10 mb0">收货地址</span>
                        <el-button type="primary" @click="addAddress()" icon="el-icon-edit" plain>添加新地址</el-button>
                    </h1>
                    <div class="o_address"  v-if='form.distribution == "normal"'>
                        <div class="o_a_item">
                            <div class="addBorder"></div>
                            <div class="o_add_inbox" v-if="address.length!=0">
                                <div class="o_a_info">
                                    <div class="o_a_name flex f-nowrap j-bet">
                                        <div><i class="el-icon-success"></i>{{address.name}}</div>
                                        <span v-if="address.is_default">默认</span>
                                    </div>
                                    <div class="o_a_phone">{{address.phone}}</div>
                                    <div class="o_a_address wto">{{address.address}}</div>
                                </div>
                                <div class="o_a_arrow">
                                    <el-button @click="editAddress(address.add_id)" icon="el-icon-edit" size="mini">编辑</el-button>
                                </div>
                            </div>
                            <div v-else-if="addList.length==0"  class="mt10 f16">您还未填写过任何收货地址</div>
                            <div v-else class="mt10 f16">请选择收货地址</div>
                        </div>
                        <!-- 地址列表 -->
                        <div class="o_list_info flex f-nowrap" v-for="(item,index) in addList" :key="index" v-if="item.add_id!=address.add_id">
                            <div class="o_list_check">
                                <el-radio value="false" @change="checkBtn(item.add_id)"></el-radio>
                            </div>
                            <div class="o_list_name  flex f-nowrap j-bet">
                                <div class="wto">{{item.name}}</div>
                                <span v-if="item.is_default">默认</span>
                            </div>
                            <div class="o_list_phone">{{item.phone}}</div>
                            <div class="o_list_addr wto">{{item.address}}</div>
                        </div>
                        <!-- 弹框 -->
                        <el-dialog title="收货地址" :visible.sync="addressVisible">
                            <el-form :model="addForm" :rules="addRules" ref="addForm" label-width="90px">
                                <el-form-item label="所在地区" prop="place">
                                    <el-input style="display: none;" @focus="selArea = true" type="text" placeholder="省份、城市、区" v-model="addForm.province+' '+addForm.city+' '+addForm.area" autocomplete="off"></el-input>
                                    <v-distpicker class="placeInput" @selected="onSelected" :province="addForm.province" :city="addForm.city" :area="addForm.area"></v-distpicker>
<!--                                    <div class="selPlace" v-show="selArea">-->
<!--                                        <v-distpicker  class="placeInput" @selected="onSelected"></v-distpicker>-->
<!--                                    </div>-->
                                </el-form-item>
                                <el-form-item label="详细地址" prop="address_detail" >
                                    <el-input type="textarea" v-model="addForm.address_detail" placeholder="详细地址，如街道，楼盘号等"></el-input>
                                </el-form-item>
                                <el-form-item label="收货人" prop="name" >
                                    <el-input v-model="addForm.name"></el-input>
                                </el-form-item>
                                <el-form-item label="手机号码" prop="mobile" >
                                    <el-input v-model.number="addForm.mobile"></el-input>
                                </el-form-item>
                            </el-form>
                            <div slot="footer" class="dialog-footer">
                                <el-button @click="addressVisible = false">取 消</el-button>
                                <el-button type="primary" @click="submitAddForm('addForm')">确 定</el-button>
                            </div>
                        </el-dialog>
                    </div>
                    <h1 class="order_tit mt30">订单信息</h1>
                    <div class="o_confirm">
                        <div class="o_c_head">
                            <div class="oc_info">订单详情</div>
                            <div class="oc_num">数量</div>
                            <div class="oc_unit">单价</div>
                            <div class="oc_count">小计</div>
                        </div>
                        <div class="oc_table">
                            <div class="oc_list" v-for="(item,index) in goods" :key="index">
                                <div class="oc_info">
                                    <div class="oc_pic"><img :src="item.pic"></div>
                                    <div class="oc_message">
                                        <h2 class="color3">{{item.name}}</h2>
                                        <p class="color9 f12">{{item.spec_name}}{{item.spec_unit}}</p>
                                    </div>
                                </div>
                                <div class="oc_num">×{{item.number}}</div>
                                <div class="oc_unit ">￥{{item.price}}/{{item.supply_unit}}</div>
                                <div class="oc_count colorPrice">￥{{(item.number*(item.price*100))/100}}</div>
                            </div>
                        </div>
                    </div>
                    <h1 class="order_tit mt30">
                        开票信息
                    </h1>
                    <div class="o_invoice">
                        <div class="w">
                            需要开具发票
                            <el-switch
                                    v-model="form.needInvoice">
                            </el-switch>
                        </div>
                        <div class="invoiceForm" v-if="form.needInvoice">
                            <div class="m_sel">
                                <div class="m_tit">选择类型：</div>
                                <div class="m_choice">
                                    <el-radio-group v-model="invoiceType" size="medium">
                                        <el-radio label="1" border>个人</el-radio>
                                        <el-radio label="0" border>企业</el-radio>
                                    </el-radio-group>
                                </div>
                            </div>
                            <div  v-if="invoiceType=='1'">
                                <el-form :model="personForm" :rules="personRules" ref="personForm"  label-width="90px">
                                    <el-form-item label="名称" prop="titleP">
                                        <el-input v-model="personForm.titleP"></el-input>
                                    </el-form-item>
                                    <el-form-item label="地址" prop="addressP">
                                        <el-input type="textarea" v-model="personForm.addressP"></el-input>
                                    </el-form-item>
                                    <el-form-item label="电话号码" prop="phoneP">
                                        <el-input v-model="personForm.phoneP" ></el-input>
                                    </el-form-item>
                                </el-form>
<!--                                <div slot="footer" class="dialog-footer tc">-->
<!--                                    <el-button type="primary" @click="submitInvoiceForm('personForm',personForm.invoice_id)">保 存</el-button>-->
<!--                                </div>-->
                            </div>
                            <div v-else>
                                <el-form :model="companyForm" :rules="companyRules" ref="companyForm" label-width="90px">
                                    <el-form-item label="公司名称" prop="ename">
                                        <el-input v-model="companyForm.ename"></el-input>
                                    </el-form-item>
                                    <el-form-item label="公司税号" prop="taxnumber" >
                                        <el-input v-model="companyForm.taxnumber"></el-input>
                                    </el-form-item>
                                    <el-form-item label="开票地址" prop="address" >
                                        <el-input type="textarea" v-model="companyForm.address" ></el-input>
                                    </el-form-item>
                                    <el-form-item label="电话号码" prop="phone" >
                                        <el-input v-model="companyForm.phone"></el-input>
                                    </el-form-item>
                                    <el-form-item label="开户银行" prop="bank">
                                        <el-input v-model="companyForm.bank"></el-input>
                                    </el-form-item>
                                    <el-form-item label="银行账号" prop="banknumber" >
                                        <el-input v-model="companyForm.banknumber"></el-input>
                                    </el-form-item>
                                </el-form>
<!--                                <div slot="footer" class="dialog-footer tc">-->
<!--                                    <el-button type="primary" @click="submitInvoiceForm('companyForm',companyForm.invoice_id)">保 存</el-button>-->
<!--                                </div>-->
                            </div>
                        </div>
                    </div>
                    <el-divider></el-divider>
                    <div class="orderText">
                        <div class="orderT_list">
                            <div class="mb10">共<span class="colorPrice">{{orderDetail.checkedGoodsCount}}</span>件商品</div>
                            <div class="mb10">实付金额：<span class="colorPrice realPay">{{orderDetail.checkedGoodsAmount}}元</span></div>
                            <div class="color3" v-if='form.distribution=="normal"&&address.address'><label>寄送至：</label>{{address.address}}</div>
                            <div class="color3" v-if='form.distribution=="normal"&&address.name'><label>收货人：</label>{{address.name}} {{address.phone}}</div>
                        </div>
                        <el-button type="primary" class="orderBtn" @click="checkForm()">生成订单</el-button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
    import {mapState, mapMutations, mapActions} from 'vuex'
    import {checkOrderInfo,submitOrder,submitOrderSingle,address,editUserAddressShow,addAddress,editUserAddress,invoice,addOrEditInvoice,delInvoice,editInvoiceShow} from '../../service/api'
    import VDistpicker from 'v-distpicker'
    import headTop from '../../components/header/headTop';

    export default {
        name: 'orderConfirm',
        data(){
            return{
                addressVisible: false,//地址弹框
                invoiceType:'1',//0是企业，1是个人
                personForm:{
                    invoice_id:'',
                    titleP:'',
                    addressP:'',
                    phoneP:''
                },
                companyForm:{
                    invoice_id:'',
                    ename:'',
                    taxnumber:'',
                    address:'',
                    phone:'',
                    bank:'',
                    banknumber:''
                },
                submitAddType:0,//0:添加地址；1：编辑修改地址
                addList:[],//地址列表
                address:[],//选中的地址
                goods:[],//产品列表
                orderDetail:[],
                addForm: {//地址表单
                    add_id:'',
                    place:'',//province+city+area
                    address_detail: '',
                    mobile:'',
                    name: '',
                    province:'',
                    city:'',
                    area:''
                },
                formLabelWidth: '90px',
                value: [],
                selArea:false,//地址选中弹出
                form:{
                    goodsId:'',
                    speId:'',
                    number:'',
                    needInvoice:false,//是否需要开发票
                    distribution:'normal',//送货方式
                    type:0//0:购物车下单，1：产品详细页下单
                },
                addRules:{
                    place: [
                        { required: true,type:"string", message: '请选择所在地区'},
                    ],
                    address_detail: [
                        { required: true, type:"string", message: '请填写详细地址',trigger: 'blur'},
                    ],
                    name: [
                        { required: true,type:"string", message: '请填写收货人',trigger: 'blur'},
                    ],
                    mobile:[
                        { required: true, message: '请填写手机号码'},
                        { type: 'number', message: '手机号必须为数字值'}
                    ]
                },
                personRules:{
                    titleP: [
                        {required:true,message:"请输入名称",trigger:"blur"}
                    ],
                    addressP: [
                        {required:true,message:"请填写地址",trigger:"blur"}
                    ],
                    phoneP
                        : [
                        { required: true, message: '请输入手机号',trigger:"blur"},
                    ]
                },
                companyRules:{
                    ename: [
                        {required:true, message:"请输入公司名称",trigger:"blur"}
                    ],
                    taxnumber: [
                        { required: true, message: '请输入公司税号',trigger:"blur"},
                    ],
                    address: [
                        {required:true, message:"请填写开票地址",trigger:"blur"}
                    ],
                    phone: [
                        { required: true, message: '请输入电话号码',trigger:"blur"},
                    ],
                    bank:[
                        { required: true, message: '请输入开户银行',trigger:"blur"},
                    ],
                    banknumber:[
                        { required: true, message: '请输入银行账号',trigger:"blur"},
                    ]
                }
            }
        },
        components:{
            VDistpicker,
            headTop
        },
        mounted(){
            this.getUserInfo();
            this.initData();
        },
        computed:{
            ...mapState([
                'userInfo','choosedAddress'
            ]),
        },
        methods:{
            ...mapActions([
                'getUserInfo'
            ]),
            ...mapMutations([
                'CHOOSE_ADDRESS'
            ]),
            //初始化数据
            async initData(){
                this.form.goodsId = this.$route.query.goodsId||'';
                this.form.speId = this.$route.query.speId||'';
                this.form.number = this.$route.query.number||0;
                this.form.type = this.$route.query.type?1:0;//1：产品详细页下单，0：购物车下单

                if (this.userInfo && this.userInfo.user_id){
                    //获取地址
                    let addressRes = await address();
                    this.addList = [...addressRes];
                    for(var i = 0 ; i < addressRes.length;i++){
                        if(addressRes[i].is_default==true){
                            this.address = addressRes[i];
                        }
                    }

                    //获取订单
                    let orderRes = await checkOrderInfo(
                        this.form.speId,
                        this.form.number
                    );
                    this.orderDetail = orderRes;
                    console.log("orderRes");
                    console.log(orderRes);

                    //获取产品
                    this.goods = orderRes.goodsInfo;

                    //获取发票
                    var invoiceRes = await invoice();
                    for(var i = 0 ; i < invoiceRes.length ; i++){
                        if(invoiceRes[i].type==0){
                            //type=0 企业类型
                            this.companyForm = invoiceRes[i];
                        }else{
                            //type=1 个人类型

                            //this.personForm = invoiceRes[i];
                            this.personForm = {
                                invoice_id:invoiceRes[i].invoice_id,
                                titleP:invoiceRes[i].title,
                                addressP:invoiceRes[i].address,
                                phoneP:invoiceRes[i].phone
                            }
                        }
                    }

                }else{

                }


            },
            onSelected(data) {
                var _this = this;
                _this.addForm.province = data.province.value;
                _this.addForm.city = data.city.value;
                _this.addForm.area = data.area.value;
                _this.addForm.place = data.province.value +' '+data.city.value+' '+data.area.value;
                _this.selArea = false;
            },
            checkBtn(add_id){
                for(var i = 0 ; i < this.addList.length;i++){
                    if(this.addList[i].add_id == add_id){
                        this.address = this.addList[i];
                    }
                }
            },
            submitAddForm(formName) {//新建或编辑地址
                this.$refs[formName].validate(async (valid) => {
                    if (valid) {
                        if(this.submitAddType==0){//0:添加地址；1：编辑修改地址
                            var res = await addAddress(
                                this.addForm.address_detail,
                                this.addForm.mobile,
                                this.addForm.name,
                                this.addForm.province,
                                this.addForm.city,
                                this.addForm.area
                            );

                        }else{
                            var res = await editUserAddress(
                                this.addForm.add_id,
                                this.addForm.address_detail,
                                this.addForm.mobile,
                                this.addForm.name,
                                this.addForm.province,
                                this.addForm.city,
                                this.addForm.area
                            );
                        }

                        if(res){
                            this.$message({
                                message: res.msg,
                                type: res.status==12000?'success':'warning'
                            });
                            if(res.status == 12000){
                                this.initData();
                                this.addressVisible = false;
                            }
                        }

                    } else {
                        return false;
                    }
                });
            },
            async editAddress(add_id){
                this.submitAddType = 1;
                this.addressVisible = true;
                var res = await editUserAddressShow(
                    add_id
                );
                this.addForm.add_id = res.address_id;
                this.addForm.place = res.province +' '+res.city+' '+res.district;
                this.addForm.address_detail = res.address_detail;
                this.addForm.mobile = parseInt(res.mobile);
                this.addForm.name = res.name;
                this.addForm.province = res.province;
                this.addForm.city = res.city;
                this.addForm.area = res.district;
            },
            addAddress(){
                if(this.addList.length>=6){
                    this.$message({
                        message: '地址已达到上限，不能超过6条哦',
                        type: 'warning'
                    });
                }else{
                    this.submitAddType = 0;
                    this.addForm = {
                        add_id:'',
                        place:'',
                        address_detail: '',
                        mobile:'',
                        name: '',
                        province:'',
                        city:'',
                        area:''
                    }
                    this.addressVisible = true;

                }
            },
            submitInvoiceForm(formName,id) {
                id = id || '';
                this.$refs[formName].validate(async (valid) => {
                    if (valid) {
                        if(this.invoiceType=='0'){//0是企业；1是个人
                            var res = await addOrEditInvoice(
                                id,//invoice_id
                                '',//title
                                this.invoiceType,//type
                                this.companyForm.taxnumber,//taxnumber
                                this.companyForm.address,//address
                                this.companyForm.banknumber,//banknumber
                                this.companyForm.phone,//phone
                                this.companyForm.bank,//bank
                                this.companyForm.ename//ename
                            )
                        }else{
                            var res = await addOrEditInvoice(
                                id,
                                this.personForm.titleP,
                                this.invoiceType,//type
                                '',//taxnumber
                                this.personForm.addressP,//address
                                '',//banknumber
                                this.personForm.phoneP,//phone
                                '',//bank
                                ''//ename
                            )
                        }
                        if(res.status!=12000){
                            this.$message({
                                message: res.msg,
                                type: 'warning'
                            });
                        }
                    } else {
                        return false;
                    }
                });
            },
            async createOrder(){
                //自提是，addID为空，物流设定时，为获取到的addressId
                var addId ='';
                if(this.form.distribution=='normal'){
                    addId = this.address.add_id?this.address.add_id:'';
                }
                //0:购物车下单-submitOrder，1：产品详细页下单-submitOrderSingle,
                    if(this.form.type == 0){
                        var res = await submitOrder(
                            this.form.distribution=='normal'?1:0,
                            addId,
                            this.form.needInvoice==false?'':(this.invoiceType=='0'?this.companyForm.invoice_id:this.personForm.invoice_id),
                            this.form.needInvoice==false?0:1,
                        )
                        if(res){
                            if(res.status == 12000){
                                //确定订单成功，进入支付页面
                                this.$router.push({path:"/order/pay",query:{orderId:''}})
                            }else{
                                this.$message({
                                    message: res.msg,
                                    type: 'warning'
                                });
                            }
                        }

                    }else{
                        var res = await submitOrderSingle(
                            this.form.distribution=='normal'?1:0,
                            addId,
                            this.form.needInvoice==false?'':(this.invoiceType=='0'?this.companyForm.invoice_id:this.personForm.invoice_id),
                            this.form.needInvoice==false?0:1,
                            this.form.speId?this.form.speId:'',
                            this.form.goodsId?this.form.goodsId:'',
                            this.form.number?this.form.number:''
                        )
                        if(res){
                            if(res.status == 12000){
                                //确定订单成功，进入支付页面
                                this.$router.push({path:"/order/pay",query:{orderId:res.orderId}})
                            }else{
                                this.$message({
                                    message: res.msg,
                                    type: 'warning'
                                });
                            }
                        }
                    }


            },
            checkForm(){
                //校验发票
                if(this.form.needInvoice == true){
                    if(this.invoiceType==1){//1是个人，0是企业
                        this.$refs['personForm'].validate(async (valid) => {
                            if (valid) {
                                this.submitInvoiceForm ('personForm',this.personForm.invoice_id);
                                this.createOrder();
                            } else {
                                return false;
                            }
                        });
                    }else{
                        this.$refs['companyForm'].validate(async (valid) => {
                            if (valid) {
                                this.submitInvoiceForm('companyForm',this.companyForm.invoice_id);
                                this.createOrder();
                            } else {
                                return false;
                            }
                        });
                    }
                }else{
                    this.createOrder();
                }

            }
        },
        created(){

        },
        watch:{
            userInfo: function (value){
                this.initData();
            }
        },

    }

</script>

<style scoped>
    /*确认订单*/
    #orderConfirm{padding:10px 0;font-size: 14px;}
    .orderBg{background-color: #fff;padding:15px;}
    .order_tit{color:#333;font-weight:bold;padding-left:10px;margin-bottom:15px;border-left:5px solid #0bb8b2;font-size:16px;}
    .uoD_item,.uoD_lf,.uoD_rg{font-size: 14px;}
    /*地址*/
    .o_address{display: flex;flex-wrap: wrap;margin-bottom:15px;background-color:#f2f2f2;}
    .o_a_item{width:100%;padding:15px;}
    .o_a_item h2{color:#333;font-size:14px;font-weight: bold;}
    .o_add_inbox{
        background-color: #fff;
        padding:10px;
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-between;
    }
    .addBorder{
        display: block;
        width: 100%;
        height: .13333rem;
        background: url(../../images/addBorder.png) repeat-x;
        background-size: .8rem .13333rem;
    }
    .o_a_info{
        width:100%;
        display: flex;
        flex-wrap: nowrap;
    }
    .o_a_name{
        width:130px;
    }
    .o_a_name .el-icon-success{
        font-size: 16px;
        color:#0bb8b2;
        margin-top:5px;
        margin-right: 10px;
    }
    .o_a_name span{
        border:1px solid #0bb8b2;
        border-radius: 3px;
        font-size: 12px;
        color:#0bb8b2;
        padding:0 10px;
        height: 24px;
        line-height: 24px;
    }

    .o_a_phone{
        font-size: 14px;
        color:#999;
        margin-top: 2px;
        width:150px;
        padding-left: 15px;
    }
    .o_a_address{
        font-size: 14px;
        color:#999;
        width:400px;
        padding-left: 15px;
    }
    .o_a_arrow{
        width:100px;
        text-align: right;
    }
    /*地址列表*/
    .o_list_info{
        padding:0 15px 10px 25px;
        width:100%;
    }
    .o_list_name{
        width:102px;
        padding-left: 15px;
    }
    .o_list_name span{
        border:1px solid #0bb8b2;
        border-radius: 3px;
        font-size: 12px;
        color:#0bb8b2;
        padding:0 10px;
        height: 24px;
        line-height: 24px;
    }
    .o_list_phone{
        font-size: 14px;
        color:#999;
        margin-top: 2px;
        width:150px;
        padding-left: 15px;
    }
    .o_list_addr{
        font-size: 14px;
        color:#999;
        width:400px;
        padding-left: 15px;
    }

    /*添加地址-弹框*/
    .selPlace{
        width:100%;
        height: 300px;
        z-index:1000;
        box-shadow: 0 0 5px 5px #ddd;
        overflow-y: visible;
        overflow-x:auto;
    }
    /*发票-弹框*/
    .invoiceForm{
        width:500px;
        margin-top:15px;
    }
    .m_sel{
        display:flex;
        flex-wrap: nowrap;
        font-size:14px;
        margin-bottom: 10px;
    }
    .m_tit{width:90px;}
    /*送货*/
    .o_sendWay{margin-bottom:15px;}

    /*确认订单信息*/
    .o_c_head{display: flex;flex-wrap: nowrap;justify-content: space-between;padding:10px 15px;background-color: #f5f5f5;}
    .oc_list{display: flex;flex-wrap: nowrap;justify-content: space-between;background-color: #fff;border:1px solid #ddd;border-top:none;padding:10px 15px;}
    .oc_table{margin:10px 0 15px 0;border-top:1px solid #ddd;}
    .oc_info{display: flex;flex-wrap: nowrap;width:100%;flex-shrink: 1;}
    .oc_num{flex-basis: 120px;text-align: center;flex-shrink: 0;}
    .oc_message{padding-left: 15px;}
    .oc_unit{flex-basis: 150px;text-align: center;flex-shrink: 0;}
    .oc_count{flex-basis: 150px;text-align: center;flex-shrink: 0;text-align: center;}
    .oc_top{background-color: #f5f5f5;color:#666;padding:10px 15px;border:1px solid #f5f5f5;}
    .oc_pic{width:80px;height: 80px;}
    .oc_pic img{width:80px;height: 80px;}
    /*开票*/
    .o_invoice{display: flex;flex-wrap: wrap;margin-bottom:15px;}
    .o_i_item{background-color:#f2f2f2;width:28%;padding:15px;margin-right: 15px;}
    .o_i_item{margin-bottom:5px;}
    /*结尾*/
    .orderText{text-align: right;}
    /*.orderBtn{background-color:#ff6600;color:#fff;height:40px;line-height: 40px;width:120px;text-align: center;display: inline-block;font-size: 18px;}*/
    .orderBtn{font-size: 22px;padding:15px 30px;margin-top:10px;}
    .realPay{font-size:26px;}
    /*格式*/
</style>



